<template>
  <div class="wrap" v-show="this.$store.state.isShowStatistics">
    <!--  -->
    <div class="leftpart">
      <div id="myChart1" class="container_1"></div>
      <div id="myChart2" class="container_2"></div>
    </div>
    <!--  -->
    <div class="rightpart">
      <div class="rightselectitems">
        <!--  -->
        <el-select
          v-model="value"
          placeholder=""
          class="selectitems"
          @change="changeItem"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
            :disabled="item.disabled"
            popper-append-to-body="false"
          >
          </el-option>
        </el-select>
        <!--  -->
        <el-date-picker
          v-model="value2"
          type="datetimerange"
          range-separator=""
          start-placeholder="start"
          end-placeholder="end"
          align="right"
          @change="changeTimeDomain"
        >
        </el-date-picker>
      </div>

      <div id="myChart3" class="container_3"></div>
      <div id="myChart4" class="container_4"></div>
    </div>
  </div>
</template>
<script>
import * as SERVICE from '../../utils/service/serviceUtils'
export default {
  data() {
    return {
      options: [
        {
          value: '',
          label: '',
        },
        {
          value: '',
          label: '',
        },
        {
          value: '',
          label: '',
        },
        {
          value: '',
          label: '',
        },
      ],
      value: '',
      value2: '',
      //
      startTime: 0,
      entTime: 0,
      //
      divideWay: '',
    }
  },
  mounted() {
    this.drawByNurseLevel()
    this.drawByDomain()
    this.drawnByWarnings()
    this.drawByServiceTimes()
  },
  methods: {
    /** */
    changeTimeDomain(event) {
      //echarts3
      this.$echarts.init(document.getElementById('myChart3')).dispose() // 
      let myChart = this.$echarts.init(document.getElementById('myChart3'))
      //echarts4
      this.$echarts.init(document.getElementById('myChart4')).dispose() // 
      let mychart4 = this.$echarts.init(document.getElementById('myChart4'))
      //
      if (!event) {
        //
        SERVICE.getRoseByNurseLevel().then((res) => {
          let data = res.data
          let legendItems = []
          if (data) {
            //
            let count = data.length - 1
            while (count >= 0) {
              legendItems.push(data[count--])
            }
            this.initEChartByOption(myChart, data, legendItems)
          }
        })
        //
        SERVICE.getBarOrderByNurseLevel().then((res) => {
          let data = res.data
          this.initEchart4ByOptions(mychart4, data)
        })
        return false
      }

      //
      if (!event || event.length == 0) return false
      //
      let start = event[0].getTime(),
        end = event[1].getTime()
      //
      //
      if (
        this.$data.divideWay &&
        this.$data.divideWay != '' &&
        this.$data.divideWay != ''
      ) {
        switch (this.$data.divideWay) {
          case '': {
            //
            SERVICE.getRoseByChinaCodeTime(start, end, '12').then((res) => {
              let data = res.data
              let legendItems = []
              if (data) {
                //
                let count = data.length - 1
                while (count >= 0) {
                  legendItems.push(data[count--])
                }
                this.initEChartByOption(myChart, data, legendItems)
              }
            })
            //
            SERVICE.getBarOrderByChinaCodeTime('12', start, end).then((res) => {
              let data = res.data
              this.initEchart4ByOptions(mychart4, data)
            })

            return
          }
          case '': {
            SERVICE.getRoseByChinaCodeTime(start, end, '34').then((res) => {
              let data = res.data
              let legendItems = []
              if (data) {
                //
                let count = data.length - 1
                while (count >= 0) {
                  legendItems.push(data[count--])
                }
                this.initEChartByOption(myChart, data, legendItems)
              }
            })
            //
            SERVICE.getBarOrderByChinaCodeTime('34', start, end).then((res) => {
              let data = res.data
              this.initEchart4ByOptions(mychart4, data)
            })
            return
          }
          case '': {
            SERVICE.getRoseByChinaCodeTime(start, end, '56').then((res) => {
              let data = res.data
              let legendItems = []
              if (data) {
                //
                let count = data.length - 1
                while (count >= 0) {
                  legendItems.push(data[count--])
                }
                this.initEChartByOption(myChart, data, legendItems)
              }
            })
            //
            SERVICE.getBarOrderByChinaCodeTime('56', start, end).then((res) => {
              let data = res.data
              this.initEchart4ByOptions(mychart4, data)
            })
            return
          }
        }
      } else {
        SERVICE.getRoseByNurseLevelTime(start, end).then((res) => {
          let data = res.data
          let legendItems = []
          if (data) {
            //
            let count = data.length - 1
            while (count >= 0) {
              legendItems.push(data[count--])
            }
            this.initEChartByOption(myChart, data, legendItems)
          }
        })
      }
    },
    /** */
    changeItem(event) {
      //
      this.$data.divideWay = event
      //echarts
      this.$echarts.init(document.getElementById('myChart3')).dispose() // 
      let myChart = this.$echarts.init(document.getElementById('myChart3'))
      this.$echarts.init(document.getElementById('myChart4')).dispose() // 
      let myChart4 = this.$echarts.init(document.getElementById('myChart4'))
      //
      switch (event) {
        case '': {
          //
          SERVICE.getRoseByNurseLevel().then((res) => {
            let data = res.data
            let legendItems = []
            if (data) {
              //
              let count = data.length - 1
              while (count >= 0) {
                legendItems.push(data[count--])
              }
              this.initEChartByOption(myChart, data, legendItems)
            }
          })
          //
          this.drawByServiceTimes()
          break
        }
        case '': {
          //
          SERVICE.getRoseByChinaCode('12').then((res) => {
            let data = res.data
            let legendItems = []
            if (data) {
              //
              let count = data.length - 1
              while (count >= 0) {
                legendItems.push(data[count--])
              }
              this.initEChartByOption(
                myChart,
                data,
                legendItems,
                '/'
              )
            }
          })
          //
          SERVICE.getBarOrderByChinaCode('12').then((res) => {
            let data = res.data
            this.initEchart4ByOptions(myChart4, data)
          })
          break
        }
        case '': {
          SERVICE.getRoseByChinaCode('34').then((res) => {
            let data = res.data
            let legendItems = []
            if (data) {
              //
              let count = data.length - 1
              while (count >= 0) {
                legendItems.push(data[count--])
              }
              this.initEChartByOption(
                myChart,
                data,
                legendItems,
                '/'
              )
            }
          })
          //
          SERVICE.getBarOrderByChinaCode('34').then((res) => {
            let data = res.data
            this.initEchart4ByOptions(myChart4, data)
          })
          break
        }
        case '': {
          SERVICE.getRoseByChinaCode().then((res) => {
            let data = res.data
            let legendItems = []
            if (data) {
              //
              let count = data.length - 1
              while (count >= 0) {
                legendItems.push(data[count--])
              }
              this.initEChartByOption(
                myChart,
                data,
                legendItems,
                '/'
              )
            }
          })
          //
          SERVICE.getBarOrderByChinaCode('56').then((res) => {
            let data = res.data
            this.initEchart4ByOptions(myChart4, data)
          })
          break
        }
      }
    },

    /** */
    initEChartByOption(
      _myChart,
      data,
      legendItems,
      seriesName = '/'
    ) {
      //
      let option = {
        title: {
          text: '',
          // subtext: '',
          textStyle: {
            color: '#fff',
          },
        },
        tooltip: {
          trigger: 'item',
          backgroundColor: 'rgba(0,0,0,0.6)',
          textStyle: {
            color: '#fff',
          },
          formatter: '{a} <br/>{b} : {c} ({d}%)',
        },
        legend: {
          // left: 'center',
          // top: 'bottom',
          top: '0%',
          right: '-1%',
          orient: 'vertical',
          textStyle: {
            color: '#fff',
            fontSize: 10,
          },
          data: legendItems,
        },
        series: [
          {
            name: seriesName,
            type: 'pie',
            radius: [20, 100],

            roseType: 'radius',
            itemStyle: {
              borderRadius: 10,
            },

            label: {
              color: '#ccc',
            },
            labelLine: {
              lineStyle: {},
            },
            emphasis: {
              label: {
                show: true,
              },
            },
            data: data,
          },
        ],
      }
      _myChart.setOption(option)
    },

    //-1:&
    drawByNurseLevel() {
      // domecharts
      let myChart1 = this.$echarts.init(document.getElementById('myChart1'))
      //
      SERVICE.getNurseLevel().then((res) => {
        let data = res.data
        if (data) {
          let option = {
            title: {
              text: '/',
              textStyle: {
                color: '#fff',
              },
            },
            legend: {
              top: '8%',
              left: '-5%',
              orient: 'vertical',
              textStyle: {
                color: '#fff',
              },
            },
            tooltip: {
              trigger: 'item',
              backgroundColor: 'rgba(0,0,0,0.6)',
              textStyle: {
                color: '#fff',
              },
            },
            series: [
              {
                name: '',
                type: 'pie',
                left: '35',
                radius: ['35%', '70%'],
                avoidLabelOverlap: false,
                label: {
                  show: false,
                  position: 'center',
                  fontSize: '12px',
                },
                emphasis: {
                  label: {
                    show: true,
                    fontSize: '18',
                    fontWeight: 'bold',
                  },
                },
                labelLine: {
                  show: false,
                },
                data: data,
              },
            ],
          }
          // 
          myChart1.setOption(option)
          setTimeout(function () {
            window.onresize = function () {
              myChart1.resize()
            }
          }, 200)
        }
      })
    },
    //-2:
    drawByDomain() {
      //
      let myChart2 = this.$echarts.init(document.getElementById('myChart2'))
      //xy
      let x_axis = [],
        y_axis = [],
        y_axis2 = []
      //
      SERVICE.getBarByChinaCode(/*56 */).then((res) => {
        let data = res.data
        if (data) {
          //
          var i = data.length - 1
          while (i >= 0) {
            x_axis.push(data[i].name)
            y_axis.push(data[i].value)
            y_axis2.push((data[i].value / data.length).toFixed(1))
            i--
          }
          //
          let option = {
            title: {
              text: '',
              textStyle: {
                color: '#fff',
              },
              left: 5,
              top: 5,
            },
            tooltip: {
              trigger: 'axis',
              backgroundColor: 'rgba(0,0,0,0.6)',
              textStyle: {
                color: '#fff',
              },
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999',
                },
              },
            },
            // legend: {
            //   data: ['', ''],
            //   textStyle: {
            //     color: '#fff',
            //   },
            // },
            xAxis: {
              type: 'category',
              data: x_axis,
              position: 'bottom',
              axisLabel: {
                show: true,
                textStyle: {
                  color: '#fff',
                },
                formatter: function (value) {
                  return value.trim().split('').join('\n')
                },
              },
              axisPointer: {
                type: 'shadow',
              },
            },
            yAxis: [
              {
                type: 'value',
                name: '',
                nameTextStyle: {
                  color: '#FF8C00',
                },
                axisLabel: {
                  color: '#fff',
                },
                axisTick: {
                  show: false,
                },
                axisLine: {
                  show: false,
                },
                splitLine: {
                  lineStyle: {
                    color: 'transparent',
                  },
                },
              },
              {
                type: 'value',
                name: '',
                nameTextStyle: {
                  color: 'yellow',
                },
                min: 0,
                max: 100,
                interval: 20,
                axisLabel: {
                  color: '#fff',
                  formatter: '{value} %',
                },
                splitLine: {
                  lineStyle: {
                    color: 'transparent',
                  },
                },
              },
            ],
            series: [
              {
                name: '',
                type: 'bar',
                itemStyle: {
                  normal: {
                    //list
                    color: function (params) {
                      // build a color map as your need.
                      var colorList = [
                        '#C1632B',
                        '#B5C334',
                        '#FCCE10',
                        '#E87C25',
                        '#27727B',

                        '#FE8463',
                        '#9BCA63',
                        '#FAD860',
                        '#F3A43B',
                        '#60C0DD',

                        '#D7504B',
                        '#C6E579',
                        '#F4E001',
                        '#F0805A',
                        '#26C0C0',
                      ]
                      return colorList[params.dataIndex]
                    }, //

                    // label: {
                    //   show: true,
                    //   position: 'top',
                    //   formatter: '{c}',
                    // },
                  },
                }, //~
                barWidth: 15,
                data: y_axis,
              },
              {
                name: '',
                type: 'line',
                yAxisIndex: 1,
                data: y_axis2,
                // label: {
                //   show: true,
                //   position: 'top',
                //   formatter: '{c}%',
                // },
              },
            ],
          }
          // 
          myChart2.setOption(option)
          //
          setTimeout(function () {
            window.onresize = function () {
              myChart2.resize()
            }
          }, 200)
        }
      })
    },
    //-3:
    drawnByWarnings() {
      let mychart3 = this.$echarts.init(document.getElementById('myChart3'))
      //
      SERVICE.getRoseByNurseLevel().then((res) => {
        let data = res.data
        let legendItems = []
        if (data) {
          //
          let count = data.length - 1
          while (count >= 0) {
            legendItems.push(data[count--])
          }
          this.initEChartByOption(mychart3, data, legendItems)
        }
      })
    },
    //-4
    drawByServiceTimes() {
      let _that = this
      let mychart4 = this.$echarts.init(document.getElementById('myChart4'))
      //
      SERVICE.getBarOrderByNurseLevel().then((res) => {
        let data = res.data
        _that.initEchart4ByOptions(mychart4, data)
      })
    },
    //4
    initEchart4ByOptions(_mychart, data) {
      //
      let value = function (value) {
        return (value + '').trim().split('').join('\n')
      }
      //option
      let option = {
        tooltip: {
          trigger: 'axis',
          backgroundColor: 'rgba(0,0,0,0.6)',
          textStyle: {
            color: '#fff',
          },
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999',
            },
          },
        },
        dataset: {
          source: data,
        },
        grid: { containLabel: true },
        xAxis: {
          name: value(data[0][1]),
          axisLabel: {
            show: true,
            textStyle: {
              color: '#fff',
            },
          },
          nameTextStyle: {
            color: 'orangered',
          },
        },

        yAxis: {
          name: data[0][2],
          type: 'category',
          axisLabel: {
            show: true,
            textStyle: {
              color: '#fff',
            },
            formatter: function (value) {
              return (value + '').trim().split('').join('\n')
            },
          },
          nameTextStyle: {
            color: 'orangered',
          },
        },
        visualMap: {
          orient: 'horizontal',
          left: 'center',
          min: 0,
          max: 100,
          text: ['High Score', 'Low Score'],
          // Map the score column to color
          dimension: 0,
          inRange: {
            color: ['#65B581', '#FFCE34', '#FD665F'],
          },
          textStyle: {
            color: 'yellow',
          },
        },
        series: [
          {
            type: 'bar',
            encode: {
              // Map the "amount" column to X axis.
              x: data[0][1],
              // Map the "product" column to Y axis
              y: data[0][2],
            },
          },
        ],
      }
      _mychart.setOption(option)
    },
  },
}
</script>
<style>
/*   */
/** */
