  background-color: transparent;
}
.el-select-dropdown__item span {
  color: #e6e6fa;
}
.el-select-dropdown__item.hover {
  background-color: transparent;
}
/*  */
.el-picker-panel,
.el-picker-panel__footer {
  color: aqua;
  background-color: rgba(0, 0, 0, 0.7);
}
.el-input.is-disabled .el-input__inner {
  color: skyblue;
  background-color: rgba(0, 0, 0, 0.7);
}
.el-picker-panel__icon-btn {
  color: aqua;
}
.el-time-panel {
  background-color: rgba(255, 255, 255, 0.9);
}
</style>

<style lang="less" scoped>
// 
.wrap {
  height: 100%;
}
.leftpart {
  position: absolute;
  bottom: 1%;
  // left: 0;
  left: 1%;
  // height: 75%;
  height: calc(100% - 230px); /** */
  // transform: translateY(-70%);
  color: #fff;
  border: 1px solid skyblue;
  background-color: rgba(10, 42, 47, 0.6);
  // z-index: 9999;
  //-1:
  .container_1,
  .container_2 {
    // margin-left: 50px;
    margin: 5px;
    width: 340px;
    height: 50%;
  }
}
// -/deep/element-ui

.rightpart {
  position: absolute;
  right: 0.5%;
  top: 50%;
  height: 98%;
  transform: translateY(-50%);
  border: 1px solid skyblue;
  background-color: rgba(10, 42, 47, 0.6);
  .rightselectitems {
    display: flex;
    width: 350px;
  }
  // 
  // .selectitems {
  //   // width: 40%;
  // }
  //-34
  .container_3,
  .container_4 {
    margin-top: 5px;
    width: 320px;
    height: 50%;
  }
  //3
  .container_3 {
    height: 35%;
  }
  //-4
  .container_4 {
    height: 60%;
  }
}
</style>
