<template>
  <div class="item-wrap">
    <!--  -->
    <span class="numid">{{ itemProperty.id }}</span>
    <!-- && -->
    <div class="generalInfo">
      {{ itemProperty.name }}
      <!--  -->
      <span class="status">{{
        itemProperty.status == '1' ? '' : ''
      }}</span>
      <!--  -->
      <span class="detail" @click="showDetail"></span>
    </div>
    <!--  -->
    <div class="datetime">{{ itemProperty.warntime }}</div>
    <!--  -->
    <div class="endtime">{{ itemProperty.endTime }}</div>
    <!--  -->
    <div class="place">{{ itemProperty.warnAddress }}</div>
  </div>
</template>
<script>
export default {
  props: {
    outProperty: Object,
    require: true,
  },
  data() {
    return {
      itemProperty: {
        id: 1,
        idNumber: '',
        name: '',
        status: '',
        warntime: '2020-01-18 18:30:00',
        endTime: '2020-01-18 18:35:26',
        wLon: '',
        wLat: '',
        warnAddress: '2001',
      },
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$data.itemProperty = undefined
      this.$data.itemProperty = this.$props.outProperty
      this.$forceUpdate()
    })
    // console.log(this.$data.itemProperty)
  },
  computed: {
    onItemProperty() {
      return this.$data.itemProperty
    },
  },
  watch: {
    onItemProperty(value) {
      // this.$data.itemProperty = value
      // console.log(value)
    },
    //
    outProperty: {
      handler(newValue, oldValue) {
        this.$data.itemProperty = this.$props.outProperty
      },
      deep: true,
    },
  },
  methods: {
    //
    showDetail() {
      // console.log(event)
      // console.log(this.$data.itemProperty)
      //
      this.$emit('showDetail', this.$data.itemProperty)
    },
  },
}
</script>
<style lang="less" scoped>
div {
  margin-top: 5px;
}
.item-wrap {
  position: relative;
  width: 300px;
  height: 90px;
  // position: absolute;
  // top: 50%;
  // left: 50%;
  font-size: 14px;
  color: #fff;
  // transform: translate(-50%, -50%);
  background-color: rgba(10, 42, 47, 0.8);
  /*  */
  .numid {
    display: inline-block;
    position: absolute;
    top: 5px;
    left: 5px;
    width: 40px;
    height: 20px;
    background-color: #ff5c26;
  }
  //&&
  .generalInfo {
    margin-left: 50px;
    display: flex;
    text-align: left;
    color: #26ffff;
    // justify-content: space-around;
    //
    .status {
      flex: 3;
      text-align: right;
      color: #92b9cc;
    }
    //
    .detail {
      flex: 2;
      text-align: right;
      padding-right: 8px;
      display: inline-block;
      color: #fff;
    }
    .detail:hover {
      cursor: pointer;
      text-decoration: underline;
      text-decoration-color: orangered;
    }
  }
  //
  .datetime {
    margin-left: 40px;
    text-align: left;
    font-size: 12px;
    color: #92bacd;
  }
  // 
  .endtime {
    margin-left: 40px;
    text-align: left;
    font-size: 12px;
    color: #92bacd;
  }
  //
  .place {
    margin-left: 40px;
    text-align: left;
    font-size: 12px;
    color: #92bacd;
  }
}
</style>
