<template>
  <div
    class="wrap"
    v-if="this.$store.state.isShowWarnInfoIndexTool"
    @mousedown="moveInfoWinwod"
  >
    <!--  -->
    <!--  -->
    <span class="title"
      ><i class="el-icon-s-help icon-style"></i> </span
    >
    <!--  -->
    <div class="chinaCode">
      <span class="type-item indexItem"></span>
      <el-select
        v-model="provinceName"
        :disabled="disabledSelectItems"
        placeholder=""
        clearable
        @visible-change="showProvince"
        @change="changeProvince"
        @focus="foucusSelect"
      >
        <el-option
          v-for="item in provinceCode"
          :key="item.value"
          :label="item.name"
          :value="item.value"
        >
        </el-option>
      </el-select>
      <el-select
        v-model="cityName"
        :disabled="disabledSelectItems"
        placeholder=""
        clearable
        @change="changeCity"
        @focus="foucusSelect"
      >
        <el-option
          v-for="item in cityCode"
          :key="item.value"
          :label="item.name"
          :value="item.value"
        >
        </el-option>
      </el-select>
      <el-select
        v-model="countryName"
        :disabled="disabledSelectItems"
        placeholder=""
        clearable
        @focus="foucusSelect"
      >
        <el-option
          v-for="item in countryCode"
          :key="item.value"
          :label="item.name"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </div>
    <!--  -->
    <div class="timespan indexItem">
      <span class="type-item"></span>
      <el-date-picker
        v-model="value1"
        type="daterange"
        range-separator=""
        start-placeholder=""
        end-placeholder=""
        @change="changeDateSpan"
        @focus="foucusSelect"
      >
      </el-date-picker>
    </div>
    <!-- -/ -->
    <div class="areaSel indexItem">
      <span class="type-item"></span>
      <el-radio-group
        v-model="radio"
        @change="changeRadioItem"
        :disabled="disabledRadioGroup"
      >
        <el-radio :label="1"></el-radio>
        <el-radio :label="2"></el-radio>
        <el-radio :label="3"></el-radio>
      </el-radio-group>
    </div>
    <!--  -->
    <div class="boundNorth">
      <div>
        <span class="item-name"></span
        ><el-input
          placeholder="53.883"
          v-model="northInputVal"
          :disabled="boundDisabled"
        >
        </el-input>
      </div>
    </div>
    <div class="boundEastWest">
      <div class="item">
        <span class="item-name"></span
        ><el-input
          placeholder="73.500"
          v-model="westInputVal"
          :disabled="boundDisabled"
        >
        </el-input>
      </div>
      <div class="item">
        <span class="item-name"></span
        ><el-input
          placeholder="137.250"
          v-model="eastInputVal"
          :disabled="boundDisabled"
        >
        </el-input>
      </div>
    </div>
    <div class="boundSouth">
      <div>
        <span class="item-name"></span
        ><el-input
          placeholder="3.517"
          v-model="southInputVal"
          :disabled="boundDisabled"
        >
        </el-input>
      </div>
    </div>
    <!--  -->
    <div class="btns indexItem">
      <el-button @click="clear"></el-button>
      <el-button @click="cancel"></el-button>
      <el-button @click="query"></el-button>
    </div>
  </div>
</template>
<script>
import * as Cesium from 'cesium/Cesium'
import * as ViewConfig from '../../utils/viewerUtils'
import * as SERVICE from '../../utils/service/serviceUtils'
import { Notification } from 'element-ui'
import DrawGraphicUtils from '../../utils/utils/DrawGraphicTools'
// import DrawGraphicUtils from '../../utils/utils/DrawGraphicTools'
export default {
  data() {
    return {
      options: [
        {
          value: '1',
          label: '',
        },
        {
          value: '2',
          label: '',
        },
        {
          value: '3',
          label: '',
        },
        {
          value: '4',
          label: '',
        },
        {
          value: '5',
          label: '',
        },
      ],
      provinceName: '', //
      cityName: '', //
      countryName: '', //
      timeStart: '', //
      timeEnd: '', //
      value1: '',
      radio: 0,
      boundDisabled: true,
      northInputVal: '', //
      westInputVal: '', //
      eastInputVal: '', //
      southInputVal: '', //
      provinceCode: {}, //
      cityCode: {}, //
      countryCode: {}, //
      //
      disabledRadioGroup: false,
      //select
      disabledSelectItems: false,
      //{1:+2}
      mode: -1,
      drawTool: undefined, //
    }
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      //
      this.$data.drawTool = new DrawGraphicUtils(ViewConfig.staticParams.viewer)
    })
  },
  computed: {
    onIndexPopUpWindow() {
      return this.$store.state.isShowWarnInfoIndexTool
    },
  },
  watch: {
    onIndexPopUpWindow(value) {
      console.log(value)
      if (value) {
        this.$data.mode = -1
        //
        this.$data.disabledRadioGroup = false
        this.$data.disabledSelectItems = false
      }
    },
  },
  methods: {
    /**
     * 
     */
    moveInfoWinwod(event) {
      console.log(event)
    },
    /** */
    foucusSelect(value) {
      console.log('')
      //
      this.$data.disabledRadioGroup = true
      //
      this.$data.mode = 1
      console.log(this.$data.mode)
    },
    /** */
    showProvince(value) {
      if (value) {
        //
        SERVICE.getProvinceOfWarn().then((res) => {
          this.$data.provinceCode = res.data
        })
      }
    },
    /** */
    changeProvince(value) {
      if (value) {
        //
        SERVICE.getCityOfWarn(value).then((res) => {
          this.$data.cityCode = res.data
        })
      }
    },
    /** */
    changeCity(value) {
      if (value) {
        SERVICE.getCountryOfWarn(value).then((res) => {
          this.$data.countryCode = res.data
        })
      }
    },
    /** */
    changeDateSpan(value) {
      console.log(value)
      if (value) {
        //
        this.$data.timeStart = value[0]
        this.$data.timeEnd = value[1]
      } else {
        //
        this.$data.timeStart = ''
        this.$data.timeEnd = ''
      }

      console.log(this.$data.timeStart)
      console.log(this.$data.timeEnd)
    },
    /**radio
     * value:radio
     */
    changeRadioItem(value) {
      console.log(value)
      //select
      this.$data.disabledSelectItems = true
      //
      this.$data.mode = 2
      console.log(this.$data.mode)

      //
      switch (value) {
        case 1: {
          console.log('')
          console.log(this.$data.drawTool.getGraphicCollection())
          //
          this.$data.drawTool.destory()
          //
          this.$data.drawTool.drawRectangle()
          //radio
          this.$data.boundDisabled = true

          //radio
          break
        }
        case 2: {
          console.log('')
          //
          this.$data.drawTool.destory()
          //
          this.$data.drawTool.drawPolygon()
          //radio
          this.$data.boundDisabled = true
          break
        }
        case 3: {
          console.log('')
          //
          this.$data.drawTool.destory()
          //radio
          this.$data.boundDisabled = false
          break
        }
      }
    },
    /**
     * 
     */
    clear() {
      this.$data.drawTool.destory() //
      //
      this.$data.drawTool.destoryHandler()
      if (
        this.$data.northInputVal ||
        this.$data.southInputVal ||
        this.eastInputVal ||
        this.westInputVal
      ) {
        this.$data.northInputVal = ''
        this.$data.southInputVal = ''
        this.$data.eastInputVal = ''
        this.$data.westInputVal = ''
      }
    },
    /** */
    cancel() {
      if (this.$data.drawTool) this.$data.drawTool.destory()
      this.$store.dispatch('setIsShowWarnInfoIndexTool', false)
    },
    /** */
    query() {
      console.log('query')
      //
      console.log(this.$data.mode)
      switch (this.$data.mode) {
        case 1: {
          console.log('')
          //
          if (
            this.$data.provinceName ||
            this.$data.cityName ||
            this.$data.countryName || //
            (this.$data.timeStart && this.$data.timeEnd)
          ) {
            //
            console.log('')
            //
            this.$store.dispatch('setIndexPopUpWindowParams', {
              mode: this.$data.mode,
              msg: {
                provinceName: this.$data.provinceName,
                cityName: this.$data.cityName,
                countryName: this.$data.countryName,
                timeStart: this.$data.timeStart
                  ? new Date(this.$data.timeStart).getTime()
                  : '',
                timeEnd: this.$data.timeEnd
                  ? new Date(this.$data.timeEnd).getTime()
                  : '',
              },
            })
          } else {
            console.log('wu')
            //
            Notification.error({
              title: '',
              message: '',
            })
          }
          break
        }
        case 2: {
          console.log('')
          //radio
          console.log(this.$data.radio)
          switch (this.$data.radio) {
            case 1: {
              //
              // console.log(this.$data.drawTool.getLonlatCollection())
              //
              this.$store.dispatch('setIndexPopUpWindowGraphicParams', {
                mode: 1,
                data: this.$data.drawTool.getLonlatCollection(),
              })

              break
            }
            case 2: {
              //
              //
              // console.log(this.$data.drawTool.getLonlatCollection())
              //
              //
              this.$store.dispatch('setIndexPopUpWindowGraphicParams', {
                mode: 2,
                data: this.$data.drawTool.getLonlatCollection(),
              })

              break
            }
            case 3: {
              //
              //
              // console.log(this.westInputVal)
              // console.log(this.eastInputVal)
              // console.log(this.northInputVal)
              // console.log(this.southInputVal)

              if (
                this.$data.northInputVal &&
                this.$data.southInputVal &&
                this.eastInputVal &&
                this.westInputVal
              ) {
                //
                this.$data.drawTool.drawRectangleByBounds(
                  parseFloat(this.northInputVal),
                  parseFloat(this.eastInputVal),
                  parseFloat(this.southInputVal),
                  parseFloat(this.westInputVal)
                )
                //
                //
                this.$store.dispatch('setIndexPopUpWindowGraphicParams', {
                  mode: 3,
                  data: {
                    north: this.northInputVal,
                    east: this.eastInputVal,
                    south: this.southInputVal,
                    west: this.westInputVal,
                  },
                })

                console.log(this.$data.drawTool.getGraphicCollection())
              } else {
                Notification.error({
                  title: '',
                  message: '',
                })
                return
              }

              break
            }
          }
          break
        }
        default: {
          Notification.error({
            title: '',
            message: '',
          })
          return false
        }
      }
    },
  },
}
</script>
<style>
.el-date-editor .el-range-separator {
  color: orchid;
}
</style>
<style lang="less" scoped>
.wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 450px;
  height: 380px;
  transform: translate(-50%, -50%);
  background-color: rgba(10, 42, 47, 0.9);
  border: 1px solid skyblue;

  .indexItem {
    margin-top: 10px;
  }
  .indexItem:first {
    margin-top: 32px;
  }
  // 
  .title {
    position: absolute;
    left: -8px;
    top: -8px;
    padding: 5px;
    display: inline-block;
    text-align: left;
    // color: #fff;
    color: greenyellow;
    border: 1px solid red;
    border-top-left-radius: 15px;
    border-bottom-right-radius: 25px;
    background-color: rgb(212, 63, 63);
    .icon-style {
      color: greenyellow;
    }
  }

  //
  .chinaCode {
    display: flex;
    justify-content: space-around;
    line-height: 45px;
    // 
    .type-item {
      color: #fff;
      padding-top: 20px;
      margin-left: 10px;
    }
    //
    .el-select {
      margin-top: 32px;
    }
  }
  //
  .timespan {
    display: flex;
    justify-content: space-around;
    // 
    .type-item {
      color: #fff;
      padding-top: 5px;
    }
  }
  //
  .areaSel {
    display: flex;
    justify-content: space-around;
    .el-radio {
      margin-top: 10px;
      color: #fff;
    }
    .el-radio:first {
      margin-left: 14px;
    }
    // 
    .type-item {
      color: #fff;
      margin-right: 14px;
      padding-top: 5px;
    }
  }
  //
  .boundNorth,
  .boundEastWest,
  .boundSouth {
    margin-top: 10px;
    color: #fff;
  }
  .boundEastWest {
    display: flex;
    justify-content: space-around;
  }
  .value-item {
    display: inline-block;
    width: 100px;
    height: 28px;
    line-height: 28px;
    border: 1px solid orange;
  }
  .item-name {
    font-weight: 700;
    color: orangered;
  }
  //
  .btns {
    display: flex;
    margin: 18px 15px 0 0;
    justify-content: flex-end;
    // 
    .el-button {
      background-color: transparent;
      font-weight: 700;
      color: rgb(38, 199, 92);
    }
  }
}
</style>
