<template>
  <div class="warpswitch">
    <!--  -->
    <i class="el-icon-sort switch-icon" title=""></i>
    <!--  -->
    <div>
      <span></span>
      <el-switch
        v-model="poiInfovalue"
        active-color="#13ce66"
        inactive-color="#ff4949"
        @change="switchPoiInfoState"
      >
      </el-switch>
    </div>
    <!--  -->
    <div>
      <span></span>
      <el-switch
        v-model="warnPtsvalue"
        active-color="#13ce66"
        inactive-color="#ff4949"
        @change="switchWarnInfoState"
      >
      </el-switch>
    </div>
    <!--  -->
    <div>
      <span></span>
      <el-switch
        v-model="servicecentervalue"
        active-color="#13ce66"
        inactive-color="#ff4949"
        @change="switchServicecenteState"
      >
      </el-switch>
    </div>
    <!--  -->
    <div>
      <span></span>
      <el-switch
        v-model="domainsvalue"
        active-color="#13ce66"
        inactive-color="#ff4949"
        @change="switchShpMapState"
      >
      </el-switch>
    </div>
  </div>
</template>
<script>
import * as ViewUtils from '../../utils/viewerUtils'
export default {
  data() {
    return {
      poiInfovalue: false, //
      warnPtsvalue: false, //
      servicecentervalue: false, //
      domainsvalue: false, //
    }
  },
  computed: {
    onWarnPtsLayerStatus() {
      return this.$store.state.warnPtsLayerStatus
    },
  },
  watch: {
    onWarnPtsLayerStatus(value) {
      if (value) {
        //
        this.$data.warnPtsvalue = value
        //
        ViewUtils.showPOIInfoPts(
          ViewUtils.staticParams.warnPOICollection,
          value
        )
      }
    },
  },
  methods: {
    /**
     * 
     */
    switchPoiInfoState(value) {
      console.log(value) //true/false
      ViewUtils.showPOIInfoPts(
        ViewUtils.staticParams.poiEntittCollection,
        value
      )
    },
    /** */
    switchWarnInfoState(value) {
      ViewUtils.showPOIInfoPts(ViewUtils.staticParams.warnPOICollection, value)
      this.$store.dispatch('setWarnPtsLayerStatus', value) // 
    },
    /** */
    switchServicecenteState(value) {
      ViewUtils.showPOIInfoPts(
        ViewUtils.staticParams.serviceCenterCollection.hospitalCollection,
        value
      )
      ViewUtils.showPOIInfoPts(
        ViewUtils.staticParams.serviceCenterCollection.servicePointCollection,
        value
      )
    },
    /** */
    switchShpMapState(value) {
      ViewUtils.showShpDataSOurce(value)
    },
  },
}
</script>
<style lang="less" scoped>
.warpswitch {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 150px;
  height: 160px;
  position: absolute;
  top: 25px;
  left: 550px;
  color: #fff;
  padding-top: 5px;
  background-color: rgba(10, 42, 47, 0.8);
  z-index: 99999;
  border: 1px solid skyblue;
  border-radius: 35px;
  border-top-left-radius: 0px;
  border-bottom-right-radius: 0px;
  // 
  .switch-icon {
    position: absolute;
    top: -5px;
    left: -5px;
    font-size: 24px;
    color: #fff;
    border: 1px solid #fff;
    background-color: orangered;
    border-radius: 50%;
  }
}
</style>
