<template>
  <div class="wrap" v-show="this.$store.state.isShowWarnDetails">
    <!--  -->
    <i class="el-icon-platform-eleme detail-icon"></i>
    <!--  -->
    <i
      class="el-icon-circle-close close-icon"
      title="close"
      @click="closeWindow"
    ></i>
    <!--  -->
    <div class="upper">
      <!--  -->
      <el-button
        class="btn-dealwarn"
        type="success"
        round
        @click="dealWarnRecord"
        ></el-button
      >
      <!--  -->
      <div class="detailInfo">
        <span class="detailinfo-title"></span>
        <div class="info-item">
          <i class="info-item-name"></i
          ><span class="info-item-value">{{ item.name }}</span>
        </div>
        <div class="info-item">
          <i class="info-item-name"></i
          ><span class="info-item-value">{{ item.warnTimes }}</span>
        </div>
        <div class="info-item">
          <i class="info-item-name"></i
          ><span class="info-item-value">{{ item.illhis }}</span>
        </div>
        <div class="info-item">
          <i class="info-item-name"></i
          ><span class="info-item-value">{{ item.age }}</span>
        </div>
        <div class="info-item">
          <i class="info-item-name"></i
          ><span class="info-item-value">{{ item.nurseLevel }}</span>
        </div>
        <div class="info-item">
          <i class="info-item-name"></i
          ><span class="info-item-value info-item-value-last">{{
            item.address
          }}</span>
        </div>
      </div>
      <!--  -->
      <div class="divices">
        <!--  -->
        <span class="divices-title"></span>

        <!--  -->
        <section class="select-selection">
          <!-- <span class="property-item"></span> -->
          <el-select
            class="select-items"
            clearable
            v-model="selectValue"
            placeholder=""
            @change="selectChange"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </section>
        <!--  -->
        <section class="dislimited">
          <!-- <span class="property-item">:</span> -->
          <el-input
            class="buffer-radius"
            v-model="bufferRadius"
            clearable
            placeholder="/m"
          ></el-input>
        </section>
        <!--  -->
        <el-button class="btn-query" type="success" round @click="loadData"
          ></el-button
        >
      </div>
    </div>
    <!--  -->
    <div class="footer">
      <el-table
        class="table-data-view"
        :data="tableData"
        height="250"
        border
        style="width: 100%"
        highlight-current-row
        @current-change="handleCurrentChange"
      >
        <el-table-column property="id" label="id" width="50"> </el-table-column>
        <el-table-column prop="name" label="" width="220">
        </el-table-column>
        <el-table-column prop="area" label=""> </el-table-column>
        <el-table-column prop="distance" label="/m"> </el-table-column>
      </el-table>
      <!--  -->
      <div class="static-marker">
         {{ totalCounts }} ,id={{ selectedId }}
      </div>
    </div>
  </div>
</template>
<script>
import { Notification } from 'element-ui' //Element-UINotification 
import * as SERVICE from '../../utils/service/serviceUtils'
import * as Viewutils from '../../utils/viewerUtils'
export default {
  data() {
    return {
      //
      outterProperty: {},
      //
      item: {
        name: '',
        warnTimes: 12,
        illhis: '',
        age: 88,
        nurseLevel: '',
        address: 'XXXXXXX111111111',
        lon: '',
        lat: '',
      },
      //
      options: [
        {
          value: '',
          label: '',
        },
        {
          value: '',
          label: '',
        },
      ],
      selectValue: '',
      ///
      bufferRadius: '',
      currentRow: null,
      totalCounts: 7, //
      selectedId: '?', //id
      tableData: [
        // {
        //   id: '1',
        //   name: '',
        //   area: ' 1518 ',
        //   distance: 10,
        // },
        // {
        //   id: '2',
        //   name: '',
        //   area: ' 1518 ',
        //   distance: 10,
        // },
        // {
        //   id: '3',
        //   name: '',
        //   area: ' 1518 ',
        //   distance: 10,
        // },
        // {
        //   id: '4',
        //   name: '',
        //   area: ' 1518 ',
        //   distance: 10,
        // },
        // {
        //   id: '5',
        //   name: '',
        //   area: ' 1518 ',
        //   distance: 10,
        // },
        // {
        //   id: '6',
        //   name: '',
        //   area: ' 1518 ',
        //   distance: 10,
        // },
        // {
        //   id: '7',
        //   name: '',
        //   area: ' 1518 ',
        //   distance: 10,
        // },
        // {
        //   id: '8',
        //   name: '',
        //   area: ' 1518 ',
        //   distance: 10,
        // },
        // {
        //   id: '9',
        //   name: '',
        //   area: ' 1518 ',
        //   distance: 10,
        // },
      ],
    }
  },
  mounted() {
    let _this = this
    this.$nextTick(() => {
      _this.$data.selectedId = '?'
    })
  },
  computed: {
    onWarnDetailOutterProperty() {
      return this.$store.state.warnDetailOutterProperty
    },
  },
  watch: {
    onWarnDetailOutterProperty(value) {
      console.log(value)
      let _this = this
      if (value) {
        this.$data.outterProperty = value
        console.log(this.$data.outterProperty)
        //idNumberitem
        let idnum = this.$data.outterProperty.idNumber
        //
        this.$data.item.lon = this.$data.outterProperty.wLon
        this.$data.item.lat = this.$data.outterProperty.wLat
        //
        SERVICE.getWarnDetailByIdnum(idnum).then((res) => {
          let data = res.data
          console.log(data)
          //
          _this.$data.item.name = data.name //
          _this.$data.item.warnTimes = data.warnTimes //
          _this.$data.item.illhis = data.illHis //
          _this.$data.item.age = data.age //
          _this.$data.item.nurseLevel = data.nurseLevel //
          _this.$data.item.address = data.address //
        })
      }
    },
  },
  methods: {
    /** */
    closeWindow() {
      console.log('close')
      //
      Viewutils.removeTempJsonLayer()
      Viewutils.removeTempMarkerLayer()
      //
      //
      this.$store.dispatch('setIsShowWarnDetails', false)
      //
      this.$store.dispatch('setWarnDetailOutterProperty', undefined)
    },
    /** */
    handleCurrentChange(val) {
      console.log('')
      this.currentRow = val
      //
      Viewutils.makeCemeraFlyTo(val.lon, val.lat)

      console.log(val)
      this.$data.selectedId = val.id
    },
    /** */
    selectChange(value) {
      //
      switch (value) {
        case '': {
          // console.log('')

          break
        }
        case '': {
          // console.log('')
          break
        }
      }
    },
    /** */
    loadData() {
      console.log(this.$data.selectValue)
      console.log(this.$data.bufferRadius)
      console.log(
        this.$data.outterProperty.wLon,
        this.$data.outterProperty.wLat
      )
      //
      if (!this.$data.selectValue) {
        Notification.error({
          title: 'Load Error',
          message: '',
          duration: 5000,
        })
        return false
      }
      //
      switch (this.$data.selectValue) {
        case '': {
          console.log('')
          //
          if (this.$data.bufferRadius) {
            //
            SERVICE.getHospitalPsByBufferDis(
              /*113.18099*/ this.$data.outterProperty.wLon,
              /*35.2248*/ this.$data.outterProperty.wLat,
              this.$data.bufferRadius
            ).then((res) => {
              let data = res.data
              if (!data || data.length == 0) {
                Notification.error({
                  title: 'Load Error',
                  message: ',',
                  duration: 5000,
                })
              } else {
                this.$data.totalCounts = res.data.length
                this.$data.tableData = res.data
                // 
                Viewutils.removeTempJsonLayer(Viewutils.staticParams.viewer)
                console.log(Viewutils.staticParams.temporaryLayers.jsonColls)
                //
                Viewutils.loadTempJsonDataSource(
                  Viewutils.staticParams.viewer,
                  this.$data.outterProperty.wLon,
                  this.$data.outterProperty.wLat,
                  this.$data.bufferRadius
                )
              }
            })
          } else {
            ////
            // 
            Viewutils.removeTempJsonLayer()
            SERVICE.getHopitalPOIListsByLonLat(
              /*113.18099*/ this.$data.item.lon,
              /*35.2248*/ this.$data.item.lat
            ).then((res) => {
              let data = res.data
              if (!data || data.length == 0) {
                Notification.error({
                  title: 'Load Error',
                  message: ',',
                  duration: 5000,
                })
              } else {
                this.$data.totalCounts = res.data.length
                this.$data.tableData = res.data
              }
            })
          }
          break
        }
        case '': {
          console.log('')
          //
          if (this.$data.bufferRadius) {
            //
            SERVICE.getServicePsByBufferDis(
              /*113.18099*/ this.$data.outterProperty.wLon,
              /*35.2248*/ this.$data.outterProperty.wLat,
              this.$data.bufferRadius
            ).then((res) => {
              let data = res.data
              if (!data || data.length == 0) {
                Notification.error({
                  title: 'Load Error',
                  message: ',',
                  duration: 5000,
                })
              } else {
                this.$data.totalCounts = res.data.length
                this.$data.tableData = res.data
                // 
                Viewutils.removeTempJsonLayer()
                console.log(Viewutils.staticParams.temporaryLayers.jsonColls)
                //
                Viewutils.loadTempJsonDataSource(
                  Viewutils.staticParams.viewer,
                  this.$data.outterProperty.wLon,
                  this.$data.outterProperty.wLat,
                  this.$data.bufferRadius
                )
              }
            })
          } else {
            ////
            // 
            Viewutils.removeTempJsonLayer()
            //
            SERVICE.getServicePOIListsByLonLat(
              /*113.18099*/ this.$data.item.lon,
              /*35.2248*/ this.$data.item.lat
            ).then((res) => {
              let data = res.data
              if (!data || data.length == 0) {
                Notification.error({
                  title: 'Load Error',
                  message: ',',
                  duration: 5000,
                })
              } else {
                this.$data.totalCounts = res.data.length
                this.$data.tableData = res.data
              }
            })
          }

          break
        }
      }
    },
    /** */
    dealWarnRecord() {
      console.log('')
      SERVICE.getServicePOIListsByLonLat('113.18099', '35.2248').then((res) => {
        console.log(res)
      })

      SERVICE.getHopitalPOIListsByLonLat(113.18099, 35.2248).then((res) => {
        console.log(res)
      })
    },
  },
}
</script>
<style lang="less">
/*  */
.el-radio__label {
  color: #fff;
}
.el-button {
  white-space: pre-wrap;
}
.el-table,
.el-table__expanded-cell {
  background-color: transparent;
}
.el-table th,
.el-table tr {
  color: #fff;
  background-color: transparent;
}
.el-table__body tr:hover > td {
  background-color: rgb(28, 120, 134) !important;
}
.el-table__body tr:focus > td {
  background-color: rgb(28, 120, 134) !important;
}
.el-table__body tr:visited > td {
  background-color: rgb(28, 120, 134) !important;
}
.el-table__body tr.current-row > td {
  background-color: #69a8ea !important;
  color: #fff;
}
</style>
<style lang="less" scoped>
// 
.wrap {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 230px;
  left: 15px;
  width: 600px;
  height: 500px;
  color: #fff;
  background-color: rgba(10, 42, 47, 0.8);
  border: 1px solid skyblue;
  border-radius: 15px;
  //
  .detail-icon {
    position: absolute;
    left: 0px;
    top: 0px;
    padding: 3px;
    font-size: 18px;
    background-color: #ff4949;
    border-top-left-radius: 15px;
    border-bottom-right-radius: 8px;
  }
  //
  .close-icon {
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
    font-size: 28px;
  }

  .upper {
    flex: 4;
    display: flex;
    //
    .btn-dealwarn {
      margin: 25px 10px;
      padding-left: 4px;
      width: 35px;
      height: 160px;
      line-height: normal;
      font-size: 24px;
      background-color: rgb(139, 192, 14);
    }
    //
    .detailInfo {
      flex: 0.6;
      margin: 25px 0 0 0px;
      // padding-right: 10px;
      padding-bottom: 5px;
      // width: 260px;
      height: 180px;
      // background-color: powderblue;
      border: 1px solid skyblue;
      //
      .detailinfo-title {
        float: right;
        padding: 3px;
        color: springgreen;
        background-color: slateblue;
        border-bottom-left-radius: 10px;
      }
      //item
      .info-item {
        padding-left: 15px;
        padding-top: 5px;
        text-align: left;
        display: flex;
        // 
        .info-item-name {
          font-style: normal;
          color: orange;
          font-weight: 700;
        }
        //
        .info-item-value {
          color: orangered;
          cursor: pointer;
          text-decoration: underline;
          text-decoration-color: skyblue;
        }
        //
        .info-item-value-last {
          // font-size: 12px;
          // overflow: hidden;
          // white-space: nowrap;
          // text-overflow: ellipsis;
        }
        .info-item-value-last:hover {
          white-space: normal; /**/
        }
      }
    }
    //
    .divices {
      flex: 0.4;
      display: flex;
      flex-direction: column;
      margin: 25px 10px 0 20px;
      padding-right: 10px;
      padding-bottom: 5px;
      // width: 150px;
      height: 180px;
      border: 1px solid skyblue;
      //
      .divices-title {
        align-self: flex-start;
        padding: 3px;
        color: springgreen;
        background-color: slateblue;
        border-bottom-right-radius: 10px;
      }
      //
      .select-selection {
        margin-top: 5px;
        display: flex;
        .property-item {
          // flex: 3;
        }
        .select-items {
          // flex: 7;
          margin: 0 auto;
          margin-top: 5px;
        }
      }
      //
      .dislimited {
        margin-top: 10px;
        display: flex;
        .buffer-radius {
          // flex: 7;
          // padding: 0;
        }
        .el-input {
          padding: 0;
          margin: 0 auto;
        }
      }
      //
      .btn-query {
        margin: 0 auto;
        margin-top: 10px;
        width: 130px;
        color: #fff;
        background-color: green;
      }
    }
  }
  .footer {
    flex: 5.5;
    margin: 5px;
    border: 1px solid skyblue;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    //
    .table-data-view {
      background-color: transparent;
    }

    //
    .static-marker {
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      background-color: orangered;
    }
  }
}
</style>
