<template>
  <div class="wrap" v-if="this.$store.state.isShowWarnInfoIndex">
    <!-- ----begin -->
    <div class="header">
      <span class="el-icon-s-tools toolbox-icon"></span>
      <div class="func-items-group">
        <i
          class="el-icon-search search-icon"
          title=""
          @click="indexTool"
          ></i
        >
        <i class="el-icon-connection deal-icon" title="" @click="dealTool"
          ></i
        >
        <i
          class="el-icon-refresh refresh-icon"
          title=""
          @click="resetTool"
          ></i
        >
      </div>
    </div>
    <!-- ----end -->
    <!--  -->
    <hr class="splitLine" />
    <!--  -->
    <div class="warn-items infinite-list-item">
      <el-scrollbar style="height: 100%">
        <WarnItem
          :outProperty="item"
          v-for="(item, index) in itemList"
          :key="index"
          @showDetail="viewDetail"
        />
      </el-scrollbar>
    </div>
  </div>
</template>
<script>
import WarnItem from '../../components/Controls/WarnItem'
import * as ViewUtils from '../../utils/viewerUtils'
import * as SERVICE from '../../utils/service/serviceUtils'
import { Notification } from 'element-ui' //Element-UINotification 
export default {
  components: {
    WarnItem,
  },
  data() {
    return {
      item: {
        id: 1,
        idnum: '41122419980268521',
        name: '',
        status: '',
        datetime: '2020-01-18',
        place: '2001',
      },
      itemList: [
        // {
        //   id: 1,
        //   name: '',
        //   status: '',
        //   datetime: '2020-01-18 18:30:00',
        //   endtime: '2020-01-18 18:35:26',
        //   place: '2001',
        // },
        // {
        //   id: 2,
        //   name: '',
        //   status: '',
        //   datetime: '2020-01-18 18:30:00',
        //   endtime: '2020-01-18 18:35:26',
        //   place: '2001',
        // },
        // {
        //   id: 3,
        //   name: '',
        //   status: '',
        //   datetime: '2020-01-18 18:30:00',
        //   endtime: '2020-01-18 18:35:26',
        //   place: '2001',
        // },
        // {
        //   id: 4,
        //   name: '',
        //   status: '',
        //   datetime: '2020-01-18 18:30:00',
        //   endtime: '2020-01-18 18:35:26',
        //   place: '2001',
        // },
        // {
        //   id: 5,
        //   name: '',
        //   status: '',
        //   datetime: '2020-01-18 18:30:00',
        //   endtime: '2020-01-18 18:35:26',
        //   place: '2001',
        // },
        // {
        //   id: 6,
        //   name: '',
        //   status: '',
        //   datetime: '2020-01-18 18:30:00',
        //   endtime: '2020-01-18 18:35:26',
        //   place: '2001',
        // },
        // {
        //   id: 7,
        //   name: '',
        //   status: '',
        //   datetime: '2020-01-18 18:30:00',
        //   endtime: '2020-01-18 18:35:26',
        //   place: '2001',
        // },
        // {
        //   id: 8,
        //   name: '',
        //   status: '',
        //   datetime: '2020-01-18 18:30:00',
        //   endtime: '2020-01-18 18:35:26',
        //   place: '2001',
        // },
        // {
        //   id: 9,
        //   name: '',
        //   status: '',
        //   datetime: '2020-01-18 18:30:00',
        //   endtime: '2020-01-18 18:35:26',
        //   place: '2001',
        // },
        // {
        //   id: 10,
        //   name: '',
        //   status: '',
        //   datetime: '2020-01-18 18:30:00',
        //   endtime: '2020-01-18 18:35:26',
        //   place: '2001',
        // },
        // {
        //   id: 11,
        //   name: '',
        //   status: '',
        //   datetime: '2020-01-18 18:30:00',
        //   endtime: '2020-01-18 18:35:26',
        //   place: '2001',
        // },
        // {
        //   id: 12,
        //   name: '',
        //   status: '',
        //   datetime: '2020-01-18 18:30:00',
        //   endtime: '2020-01-18 18:35:26',
        //   place: '2001',
        // }
      ],
      indexWarnTime: '',
    }
  },
  mounted() {
    this.$nextTick(function () {
      SERVICE.getAllWarnRecords().then((res) => {
        let data = res.data
        console.log(data)
        console.log(data.length)
        if (!data || data.length == 0) {
          Notification.success({
            title: 'Loading Error',
            message: '',
            duration: 3000,
          })
          //
          this.$data.itemList = []
          return false
        }
        //
        this.$data.itemList = res.data
      })
    })
  },
  computed: {
    //
    onIndexPopUpWindowParams() {
      return this.$store.state.indexPopUpWindowParams
    },
    onIndexPopUpWindowGraphicParams() {
      return this.$store.state.indexPopUpWindowGraphicParams
    },
  },
  watch: {
    //
    onIndexPopUpWindowParams(value) {
      console.log(value)
      /*
      mode: 1
      msg: {
        cityName: ""
        countryName: ""
        provinceName: 410000
        timeEnd: NaN
        timeStart: NaN

      }
      */
      //
      let searchMode = value.mode
      let msg = value.msg
      console.log(msg)
      if (searchMode == 1) {
        console.log('send jsondata')
        //
        SERVICE.sendJsonData({
          msg,
        }).then((res) => {
          let data = res.data
          console.log(data)
          if (!data || data.length == 0) {
            Notification.warning({
              title: 'Loading Error',
              message: '',
              duration: 3000,
            })
            //
            this.$data.itemList = []
            return false
          }
          //
          this.$data.itemList = []
          //
          let len = res.data.length - 1
          while (len >= 0) {
            this.$data.itemList.unshift(data[len--])
          }
          console.log(this.$data.itemList)
        })
      }
    },
    onIndexPopUpWindowGraphicParams(value) {
      if (value) {
        switch (value.mode) {
          case 1: {
            //
            SERVICE.getWarnInfoByRectangle(value.data).then((res) => {
              let data = res.data
              console.log(data)
              if (!data || data.length == 0) {
                Notification.warning({
                  title: 'Loading Error',
                  message: '',
                  duration: 3000,
                })
                //
                this.$data.itemList = []
                return false
              }
              //
              this.$data.itemList = []
              //
              let len = res.data.length - 1
              while (len >= 0) {
                this.$data.itemList.unshift(data[len--])
              }
              console.log(this.$data.itemList)
            })
            break
          }
          case 2: {
            //
            SERVICE.getWarnInfoByBounds(value.data).then((res) => {
              console.log(res)
              //
              let data = res.data
              console.log(data)
              if (!data || data.length == 0) {
                Notification.warning({
                  title: 'Loading Error',
                  message: '',
                  duration: 3000,
                })
                //
                this.$data.itemList = []
                return false
              }
              //
              this.$data.itemList = []
              //
              let len = res.data.length - 1
              while (len >= 0) {
                this.$data.itemList.unshift(data[len--])
              }
              console.log(this.$data.itemList)
            })
          }
          case 3: {
            //
            //
            SERVICE.getWarnRecordByBoundPts(
              value.data.north,
              value.data.east,
              value.data.south,
              value.data.west
            ).then((res) => {
              let data = res.data
              console.log(data)
              if (!data || data.length == 0) {
                Notification.warning({
                  title: 'Loading Error',
                  message: '',
                  duration: 3000,
                })
                //
                this.$data.itemList = []
                return false
              }
              //
              this.$data.itemList = []
              //
              let len = res.data.length - 1
              while (len >= 0) {
                this.$data.itemList.unshift(data[len--])
              }
              console.log(this.$data.itemList)
            })
          }
        }
        value = ''
      }
    },
  },
  methods: {
    /** */
    indexTool() {
      this.$store.dispatch('setIsShowWarnInfoIndexTool', true)
    },
    /** */
    resetTool() {
      console.log('resetTool')
      SERVICE.getAllWarnRecords().then((res) => {
        let data = res.data
        if (!data || data.length == 0) {
          Notification.warning({
            title: 'Loading Error',
            message: '',
            duration: 3000,
          })
          //
          this.$data.itemList = []
          return false
        }
        //
        this.$data.itemList = res.data
      })
    },
    /** */
    dealTool() {
      console.log('dealTool')
      SERVICE.getUnResolvedWarnRecords().then((res) => {
        let data = res.data
        if (!data || data.length == 0) {
          Notification.warning({
            title: 'Loading Error',
            message: '',
            duration: 3000,
          })
          //
          this.$data.itemList = []
          return false
        }
        //
        this.$data.itemList = res.data
      })
    },
    /**WarnItem */
    viewDetail(value) {
      console.log(value)
      if (value) {
        //
        switch (value.status) {
          case 0: {
            //
            // this.$store.dispatch('setWarnPtsLayerStatus', true) // 
            //
            //             wLat: 35.1764
            // wLon: 113.197
            //
            this.$store.dispatch('setIsShowWarnDetails', true)
            //
            this.$store.dispatch('setWarnDetailOutterProperty', value)
            //
            ViewUtils.removeTempMarkerLayer()
            ViewUtils.removeTempJsonLayer()
            //
            ViewUtils.loadTempMarker(value.wLon, value.wLat)
            break
          }
          case 1: {
            //
            //
            this.$store.dispatch('setIsShowPopUpWindow', true)
            //
            this.$store.dispatch('setPopWindowItemProperty', {
              idnum: value.idNumber,
            })
            break
          }
        }
      }
    },
  },
}
</script>
