export function initClientUserInfo(_viewer) {
    SERVICE.getAllClientUserList().then((res) => {
        if (res.status == 200) {
            console.log(res)
            let data = res.data
                //
            Notification.success({
                    title: 'Loading Success...',
                    message: ',!',
                    duration: 5000,
                })
                //
                //
            var i = data.length - 1
            console.log(data[i].nurselevel)
            while (i >= 0) {
                createSingleEntityPOI(staticParams.viewer, data[i--])
            }
        } else {
            Notification.error({
                title: 'Loading Error',
                message: ',',
                duration: 5000,
            })
        }
    })
}

/**
 * Entity
 * @param {} _viewer
 * @param {POI} _poiInfo
 */
export function createSingleEntityPOI(_viewer, _poiInfo) {
    // let imgUrl = require('../../static/imgs/home/mark3.png')
    // console.log(imgUrl)
    //
    let imgUrl = 'greenMarker' //-

    switch (_poiInfo.nurselevel) {
        case '':
            {
                imgUrl = 'redMarker' //
                break
            }
        case '':
            {
                imgUrl = 'orangeMarker' //
                break
            }
        case '':
            {
                imgUrl = 'yellowMarker' //
                break
            }
        case '':
            {
                imgUrl = 'blueMarker' //
                break
            }
        default:
            {
                imgUrl = 'greenMarker' //
                break
            }
    }
    //Cesium.Entity
    let entity = new Cesium.Entity({
            //(Cesium)
            position: Cesium.Cartesian3.fromDegrees(
                _poiInfo.lon, //
                _poiInfo.lat, //
                0.0 //height:default value
            ),
            //
            billboard: {
                //
                image: require('../../static/imgs/home/' + `${imgUrl}` + '.png'),
                width: 24,
                height: 24,
            },
            //
            label: {
                // text: _poiInfo.name,
                // backgroundColor: Cesium.Color.TRANSPARENT,
                // font: 'small-caps bold 12px/1 sans-serif',
                // pixelOffset: new Cesium.Cartesian2(10, -15), //
            },
            //
            // point: {
            //     color: Cesium.Color.RED, //
            //     pixelSize: 15, //
            //     show: true, //
            // },
            name: _poiInfo.idnum,
            show: false, //
        })
        //
    staticParams.poiEntittCollection.push(entity)
        //EntityViewer
    _viewer.entities.add(entity)
}

