/**
 * marker
 */
export function removeTempMarkerLayer() {
    let len_marker = staticParams.temporaryLayers.markerColls.length - 1
    while (len_marker >= 0) {
        staticParams.viewer.entities.remove(
            staticParams.temporaryLayers.markerColls[len_marker--]
        )
    }
    staticParams.temporaryLayers.markerColls = []
}

/**
 * 
 * @param {} _viewer
 */
export function initServiceCenters(_viewer) {
    /**
     *
     * @param {} _viewer
     * @param {} _ptInfo
     * @param {1-2-} _mode
     * @param {} imgurl
     */
    function createWarnPoint(_viewer, _ptInfo, _mode, imgurl) {
        //Cesium.Entity
        let entity = new Cesium.Entity({
                //(Cesium)
                position: Cesium.Cartesian3.fromDegrees(
                    _ptInfo.lon, //
                    _ptInfo.lat, //
                    0.0 //height:default value
                ),
                //
                billboard: {
                    //
                    image: imgurl,
                    width: 24,
                    height: 24,
                },
                name: _ptInfo.name,
                show: false, //
                label: {
                    text: _ptInfo.name,
                    font: '12px Helvetica',
                    fillColor: Cesium.Color.CORAL,
                    outlineColor: Cesium.Color.BLACK,
                    outlineWidth: 2,
                    style: Cesium.LabelStyle.FILL_AND_OUTLINE,
                    pixelOffset: new Cesium.Cartesian2(15, 5),
                },
            })
            //
        _mode == 1 ?
            staticParams.serviceCenterCollection.hospitalCollection.push(entity) :
            staticParams.serviceCenterCollection.servicePointCollection.push(entity)
            //EntityViewer
        _viewer.entities.add(entity)
    }
    let srcHos = require('../../static/imgs/home/hospital.png'),
        srcCenters = require('../../static/imgs/home/servicecenter.png')

    //
    SERVICE.getAllHospital().then((res) => {
            if (res.data && res.data.length != 0) {
                console.log(res)
                let data = res.data
                    //
                Notification.success({
                        title: 'Loading Success...',
                        message: ',!',
                        duration: 5000,
                    })
                    //
                    //
                var i = data.length - 1
                while (i >= 0) {
                    createWarnPoint(_viewer, data[i--], 1, srcHos)
                }
            } else {
                Notification.error({
                    title: 'Loading Error',
                    message: ',',
                    duration: 5000,
                })
            }
        })
        //
    SERVICE.getAllServicePoint().then((res) => {
        if (res.data && res.data.length != 0) {
            console.log(res)
            let data = res.data
                //
            Notification.success({
                    title: 'Loading Success...',
                    message: ',!',
                    duration: 5000,
                })
                //
                //
            var i = data.length - 1
            while (i >= 0) {
                createWarnPoint(_viewer, data[i--], 2, srcCenters)
            }
        } else {
            Notification.error({
                title: 'Loading Error',
                message: ',',
                duration: 5000,
            })
        }
    })
}

